import { Component, OnInit } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { DialogService } from 'src/app/api/util/dialog.service';

@Component({
  selector: 'app-messaging',
  templateUrl: './messaging.page.html',
  styleUrls: ['./messaging.page.scss'],
})
export class MessagingPage implements OnInit {


  public modelMessaging : Model;

  public results = [];

  public requests = [
    {
      name : 'Not Partidos Creados' ,
      functionName : 'showGamesLoaded'
    },
    {
      name : 'Resultados cargados' ,
      functionName : 'loadedResults'
    },
    {
      name : 'Aviso para crear partidos' ,
      functionName : 'adminCreateGame'
    },
    {
      name : 'Tercer tiempo' ,
      functionName : 'after'
    },{
      name : 'AÃ±adir resultados a los partidos',
      'functionName' : 'addResultGames'
    },
    {
      name : 'Promicion de la pagina de Facebook',
      'functionName' : 'pageFacebook'
    }
  ]

  constructor(
    private requestService : RequestService,
    public dialogService : DialogService
  ) { 
    this.modelMessaging= new Model('Massaging' , requestService)
  }

  ngOnInit() {
  }

  async run(item){
    await this.dialogService.loadingProgres();

    this.modelMessaging.api_function(item.functionName).subscribe(
      response => {
        if(response['status'] == 'success'){
          this.results = response['data'];
        }
        this.dialogService.dimissLoaging();
      },
      error => {
        this.dialogService.dimissLoaging();
      }
    )

  }

  clearResults(){
    this.results = [];
  }

  get sends(){
    return this.results.filter((element) => {
      return element && element.result && element.result.success == 1;
    })
  }

  get notSends(){
    return this.results.filter((element) => {
      return element && element.result && element.result.success == 0;
    })
  }

}
